



















html, body, #app {
    width: 100%;
    height: 100%;

    overflow: hidden;

    padding: 0;
    margin: 0;
}

.fade-transition {
  -webkit-transition: opacity .3s ease;
  transition: opacity .3s ease;
}
.fade-enter, .fade-leave {
  opacity: 0;
}



